import bg11Image from 'assets/img/bg/background_1920-11.jpg';
import landingImage from 'assets/img/logo/landing-2-image.png';
import bg18Image from 'assets/img/bg/background_1920-18.jpg';
import bg1Image from 'assets/img/bg/background_640-1.jpg';
import bg3Image from 'assets/img/bg/background_640-3.jpg';
import user1Image from 'assets/img/users/100_1.png';
import { UserCard } from 'components/Card';
import Page from 'components/Page';
import React from 'react';

import PageSpinner from 'components/PageSpinner';

import AsyncStorage from '@callstack/async-storage';
import { renewAccountPay, isRenewed } from "../../api/api";

import {
  Form,
  FormFeedback,
  FormGroup,
  FormText,
  Input,
  Label,
  Button,
  Card,
  CardBody,
  CardImg,
  CardImgOverlay,
  CardLink,
  CardText,
  CardTitle,
  Col,
  ListGroup,
  Alert,
  ListGroupItem,
  Row,
} from 'reactstrap';
import config from '../../api/config';

class UserRenewPage extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            payid: '',
            selected_app:'',
            xtoken: '',
            g_phone:'',
            msg: '',
            error: '',
            preload:false,
            showform: true,
            showFinish:false,
        };
        AsyncStorage.getItem(config.secret)
        .then((results) => {
          if( results !== null )
          {
            let object = JSON.parse(results);
            this.setState({
              ...this.state,
              selected_app : object.data.app_label,
              g_phone: object.data.info.g_phone,
              xtoken: object.data.token,
            });
          }
        })
        .catch((error) => {
          console.log('storage error', error);
        });
    }
    handleFinish = () => {
        this.setState({...this.state, preload:true,});
        isRenewed(this.state.xtoken)
        .then((response) => {
            if(response.status === 200 && response.done === 1 )
            {
                AsyncStorage.setItem(config.secret, JSON.stringify(response)).then(() => {}).catch((err) =>{});
                window.location.href = '/';
                return;
            }
            else
            {
                this.setState({
                    ...this.state,
                    error: response.message,
                    msg: '',
                    showform:true,
                    showFinish: false,
                    preload:false,
                });
            }
        })
        .catch((error) => {
            this.setState({
                ...this.state,
                msg: '',
                error: 'Connection error occured. Try again',
                preload:false,
            });
        });
    }
    handleGPhoneInput = (event) => {
        const v = event.target.value;
        this.setState({...this.state, g_phone: v,});
    }
    handleInitiateTransaction = (event) => {
        this.setState({...this.state, preload:true,});
        let postData = {
            phone: this.state.g_phone,
        }
        renewAccountPay(this.state.xtoken, postData)
        .then((response) => {
            console.log('response', response);
            if( response.status === 200 )
            {
                this.setState({
                    ...this.state,
                    msg: response.message,
                    error: '',
                    showform:false,
                    showFinish: true,
                    preload:false,
                });
                return;
            }
            else
            {
                this.setState({
                    ...this.state,
                    msg: '',
                    error: response.message,
                    showform:true,
                    showFinish: false,
                    preload:false,
                });
                return;
            }
        })
        .catch((error) => {
            this.setState({
                ...this.state,
                msg: '',
                error: 'Connection error occured. Try again',
                preload:false,
            });
            return;
        });
    }
    render() {
        
        return(
        <Page title="Cards" breadcrumbs={[{ name: 'cards', active: true }]}>
        <Row className="justify-content-center">
            <Col md={8} sm={8} xs={12} className="mb-1">
                <Card className="flex-row text-center">
                    <CardBody>
                        <CardTitle><h1>Renew Subscription<br></br></h1></CardTitle>
                        <small>Your subscription for <b>{this.state.selected_app}</b> has expired. Kindly renew to continue enjoying our services</small>
                        {this.state.preload && (
                            <CardTitle><PageSpinner/></CardTitle>
                        )}
                        {this.state.error.length > 0 && (
                            <Alert color="danger">{this.state.error}</Alert>
                        )}
                        {this.state.msg.length > 0 && (
                            <Alert color="success">{this.state.msg}</Alert>
                        )}
                        <br></br>
                        <div className="row justify-content-center">
                            <div className="col-md-6 col-sm-6 col-xs-12">
                                <Form>
                                    {this.state.showform === true && (
                                        <div>
                                            <FormGroup>
                                                <Label>Guardian/Parent <b>Mpesa</b> Phone</Label>
                                                <Input
                                                    onChange={this.handleGPhoneInput} 
                                                    value={this.state.g_phone}
                                                    type="number"
                                                    name="phone"
                                                    placeholder=""
                                                />
                                            </FormGroup>
                                            <br></br>
                                            <div className="row">
                                                <div className="col-md-12 col-sm-12 col-xs-12">
                                                    <Button onClick={this.handleInitiateTransaction} className="btn-custom">Complete Payment</Button>
                                                </div>
                                            </div>
                                        </div>
                                    )}
                                    {this.state.showFinish === true && (
                                        <div className="row justify-content-center">
                                            <div className="col-md-8 col-sm-12 col-xs-12">
                                                <p>If you have paid and received MPESA message, click on button below to finish</p>
                                                <Button onClick={this.handleFinish} className="btn-custom">
                                                    Click here to complete
                                                </Button>
                                            </div>
                                        </div>
                                    )}
                                </Form>
                            </div>
                        </div>
                    </CardBody>
                </Card>
            </Col>
        </Row>
        </Page>
    );
 }
};
export default UserRenewPage;
